import { Component, OnInit, ViewChild, ViewChildren, Input, OnDestroy } from '@angular/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar';
import { ChatService, ChatCollection, User, Chat } from '../../../chat/chat.service';
import { NgForm } from '@angular/forms';
import { Subscription } from 'rxjs';
import { SharedAnimations } from '../../../../shared/animations/shared-animations';
import { FinancialService } from '../../../../shared/services/financial.service';
import { Router, ActivatedRoute } from '@angular/router';
import { AuditService, Audit } from '../../../../shared/services/audit.service';
import { AttachmentService } from '../../../../shared/services/attachment.service';
import { saveAs } from 'file-saver';
import { AuditResponse, AuditEvent } from '../../../../shared/models';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Financial } from '../../../../shared/models/financial';

@Component({
  selector: 'fins-content',
  templateUrl: './content.component.html',
  styleUrls: ['./content.component.scss'],
  animations: [SharedAnimations]
})
export class ContentComponent implements OnInit {

  activeFinancial: Financial = new Financial();

  loadAuditDataSub: Subscription;
  financialSelectSub: Subscription;
  financialId: number;

  showHistory = true;
  audits: AuditEvent[];
  auditEventDetail: string[];


  // tslint:disable-next-line:no-input-rename
  @Input('matSidenav') matSidenav;
  @ViewChild(PerfectScrollbarDirective) psContainer: PerfectScrollbarDirective;

  @ViewChildren('msgInput') msgInput;
  @ViewChild('msgForm') msgForm: NgForm;

  constructor(public service: FinancialService,
    private auditService: AuditService,
    private router: Router,
    private modalService: NgbModal,
    private attachmentService: AttachmentService,
    private route: ActivatedRoute) {

  }

  ngOnInit() {
    this.route.params.subscribe(params => this.financialId = params['id']);
    // Listen for company change
    this.financialSelectSub = this.service.onFinancialSelected.subscribe(res => {
      if (res) {
        this.activeFinancial = res.financial;
        this.showHistory = false;
      }
    });
  }

  ngOnDestroy() {
    if (this.financialSelectSub) { this.financialSelectSub.unsubscribe(); }
  }

  

  edit(financial) {
    this.router.navigate(['edit', financial.id], { relativeTo: this.route });
  }

  toggleShowAudit() {
    this.showHistory = !this.showHistory;
    if (this.showHistory) {
      this.loadAuditDataSub = this.auditService.getAuditTrails('company', this.activeFinancial.id)
        .subscribe(res => {
          this.audits = res.events;
        });
    }
  }

  clone(financial: Financial) {
    this.router.navigate(['clone', financial.id], {relativeTo: this.route})
  }

  confirm(content) {
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title', centered: true })
    .result.then((result) => {
      console.log("model delete : " + result);
    }, (reason) => {
      console.log("model reason : " + reason);
    });
  }

  open(content, eventId) {
    this.auditService.getAuditEventDetail(eventId).subscribe(
      response => {
        debugger;
        this.auditEventDetail = response.changeDetails;
        this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' })
      .result.then((result) => {
        console.log(result);
      }, (reason) => {
        console.log('Err!', reason);
      });
      },
      error => {
        console.log(error);
      }
    )
    
  }

}
